import Row from "react-bootstrap/Row";
import svg404 from "./assets/404.svg";

import ReactGA from "react-ga";

function NoMatch({ component }) {
  ReactGA.initialize("UA-71648045-1");
  ReactGA.pageview("/404Page");

  return (
    <Row className="NomatchBox">
      <h1>Sorry but the above page we are also looking for right now.. </h1>
      <img src={svg404} alt="404 page not found illustration.." />
    </Row>
  );
}

export default NoMatch;
