import {
  FaFacebook,
  FaGithub,
  FaLinkedin,
  FaTwitter,
  FaInstagram,
  FaMedium,
} from "react-icons/fa";
function Contacts() {
  return (
    <div className="contactBox">
      <a
        href="https://www.facebook.com/Rockstarsivam/"
        target="_blank"
        title="faceBook"
      >
        <FaFacebook />
      </a>
      <a href="https://github.com/sivamspandey" target="_blank" title="gitHub">
        <FaGithub />
      </a>
      <a
        href="https://www.linkedin.com/in/sivamspandey/"
        target="_blank"
        title="linkedin"
      >
        <FaLinkedin />
      </a>
      <a
        href="https://twitter.com/sivamspandey"
        target="_blank"
        title="twitter"
      >
        <FaTwitter />
      </a>
      <a
        href="https://www.instagram.com/sivamspandey/"
        target="_blank"
        title="instagram"
      >
        <FaInstagram />
      </a>
      <a href="https://medium.com/@sivamspandey" target="_blank" title="medium">
        <FaMedium />
      </a>
    </div>
  );
}

export default Contacts;
